@import url(https://fonts.googleapis.com/css2?family=Parisienne&family=Prociono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  height: 100vh;
  width: 100vw;
}

.App {
  height: 100vh;
  width: 100vw;
}

h1 {
  font-family: Parisienne;
  color: #FA0CFF;
  font-size: 96px;
  font-weight: 400;
}

/* Nav */
.nav {
  height: 20rem;
  width: 100vw;
  position: relative;
}

.navMainBox {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.navimg {
  height: 80%;
  object-fit: contain;
  border-radius: 25px;
}

.navButtonBox {
  display: flex;
  flex-direction: row;
  width: 60%;
  justify-content: space-around;
}

.navButtonBox-li {
  list-style: none;
  background-color: #C598C0;
  width: 113px;
  height: 37px;
  border-radius: 10px;
  text-align: center;
  line-height: 37px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.navButtonBox-li-link {
  text-decoration: none;
  font-family: 'Prociono', serif;
  color: black;
  font-weight: lighter;
}

@media only screen and (max-width: 1000px) {
  .navButtonBox {
    width: 100%;
  }
}
@media only screen and (max-width: 800px) {
  .navimg {
    max-width: 100%;
  }
}

@media only screen and (max-width: 800px) {

  .nav {
    display: flex;
    flex-direction: column;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }

  .navButtonBox {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
  }
  .navButtonBox-li {
    width: 70%;
    margin: 4px;
    height: 3rem;
    line-height: 45px;
  }
  
  .navimg {
    height: auto;
  }

}

/* About */

.about{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.about-header {
  font-family: Parisienne;
  color: #FA0CFF;
  font-size: 96px;
  font-weight: 400;
}

.about span {
  color: #FA0CFF;
}

.about-text {
  display: flex;
  flex-direction: row;
  width: 70%;
  margin-top: 2rem;
  margin-bottom: 1rem;
}

@media only screen and (max-width: 900px) {
  .about-text {
    width: 90%;
  }
}

.about-text-img {
  width: 18px;
  height: 17px;
}

.about-text span {
  color: black;
  font-weight: bold;
}

/* Lingerie */

.lingerie {
  display: flex;
  flex-direction: column;
  align-items: center;

}

.lingerie p {
  text-align: center;
}

.lingerie-text {
  display: flex;
  flex-direction: row;
  text-align: center;
  width: 70%;
}

.lingerie-text-image {
  width: 18px;
  height: 17px;
}

.lingerieGallery{
  position: relative;
  display: flex;
  justify-content: center;
  flex-wrap:wrap;
  width: 60%;
  margin-top: 1rem;
}

.lingerGallery-img {
  margin: 2px;
  width: 20%;
  object-fit: cover;
  outline: 1px solid #FCDDEC;
  border-radius: 25px;
}

@media only screen and (max-width: 1300px) {

  .lingerie-text {
    width: 90%;
  }

  .lingerGallery-img {
    width: 30%;
  }
}
@media only screen and (max-width: 900px) {
  .lingerGallery-img {
    width: 80%;
  }
}
@media only screen and (max-width: 700px) {
  .lingerGallery-img {
    width: 100%;
  }
}

/* Sewing */

.sewing {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sewingBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 70%;
  margin-top:1rem;
}

@media only screen and (max-width: 700px) {
  .sewingBox {
    width: 90%;
  }
}

.sewingBox-subHeading {
  font-family: Parisienne;
  color: #A31E95;
  -webkit-text-stroke: .5px #000000;
  font-size: 36px;
  font-weight: 400;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 5px;
}

.sewingBox-text {
  display: flex;
  flex-direction: row;
}

.sewingBox-text-img {
  width: 18px;
  height: 17px;
  margin-right: 5px;
}

.sewingBox-text-p {

}

/* gifts */

.gifts {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.giftsBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 70%;
  margin-top: 1rem;
}

@media only screen and (max-width: 900px) {
  .giftsBox {
    width: 90%;
  }
}

.giftsBox-subHeading {
  font-family: Parisienne;
  color: #A31E95;
  -webkit-text-stroke: .5px #000000;
  font-size: 36px;
  font-weight: 400;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 5px;
}

.giftsBox-text {
  display: flex;
  flex-direction: row;
}

.giftsBox-text-img {
  width: 18px;
  height: 17px;
  margin-right: 5px;
}

/* Contact  */

.contact {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-text-p {
  font-family: Quicksand;
  color: #8E0D62;
  font-weight: 900;
  font-size: 18px;
}

.contactForm {
  font-family: Quicksand;
  text-align: center;
  color: #8E0D62;
  font-weight: 900;
  font-size: 18px;
  min-width: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.contactForm input {
  height: 2rem;
}

.contactForm textarea {
  width: 80%;
  outline: 2px solid #C598C0;;
  border: none;
  resize: none;
}

.contactForm label {
  padding: 1%;
}

.contactForm-group1 {
  display: flex;
  flex-direction: row;
}

.contactForm select {
  width: 10rem;
  text-align: center;
  height: 2rem;
}

.contactForm button {
  margin-top: 5px;
  padding: 1%;
  border-radius: 50px;
  width: 10rem;
  border: none;
  background-color: #C598C0;
  transition: background-color 300ms;
}

.contactForm button:active {
  background-color: #fa0bff;
}

/* submitted page*/

.submitted {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
} 

.submitted-text {
  font-family: Quicksand;
  color: #8E0D62;
  font-weight: 900;
  font-size: 20px;
}

.submitted-button {
  margin-top: 1rem;
  list-style: none;
  text-decoration: none;
  background-color: #C598C0;
  color: black;
  width: 113px;
  height: 37px;
  border-radius: 10px;
  text-align: center;
  line-height: 37px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
}

/* not found */
.notfound {
  display: flex;
  height: 10rem;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: Quicksand;
  color: #8E0D62;
  font-weight: 900;
  font-size: 20px;
}

.notfoundBtn {
  margin-top: 1rem;
  list-style: none;
  text-decoration: none;
  background-color: #C598C0;
  color: black;
  width: 180px;
  height: 37px;
  border-radius: 10px;
  text-align: center;
  line-height: 37px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-weight: 100;
}
